import React,{Component} from 'react';
import './Skills.css'
import './Header.css'
export class Skills extends Component{
    constructor(props){
        super(props);
        this.state = {skillCount:1};
    }
    addSkills(){
        let skill=this.state.skillCount;
        skill++;
        console.log("skills added");
        //this.state.skillCount=skill;
        this.setState({skillCount:skill});
    }
    render(){
    return(<div>
        <h2 className='blue'>Skills Component</h2>
        <h3 className='red'>Red class of header</h3>
        <button onClick={this.addSkills.bind(this)}>Add Skills</button>
        <p>Total skills are {this.state.skillCount}</p>
    </div>) 
}
}
